/* Hamburger */
body {
  color: #666;
  height: 100%;
}
.wrapper {
  width: 100%;
  min-height: 100%;
  height: auto !important;
  height: 100%;
  min-width: 320px;
  margin: 0 auto;
}
.container.size {
  max-width: 960px;
  padding: 0;
}
@media only screen and (max-width: 768px) {
  .container.size {
    padding: 0 15px;
  }
}
#header {
  background: #272727;
}
#header .logo {
  float: left;
  margin-right: 18px;
  line-height: 3rem;
  height: 100%;
}
#header .logo span {
  display: block;
  width: 128px;
  min-height: 48px;
  height: 100%;
  background-image: url('/repair/images/ASlogo.svg');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: 50%;
}
#header .select-city-menu {
  float: left;
  color: #ffffff;
  height: 3rem;
}
#header .select-city-menu .mcs-module {
  position: relative;
  width: 200px;
  text-align: left;
  padding: 15px 0;
}
#header .select-city-menu .mcs-module .city {
  color: #ffffff;
  font-size: 13px;
  text-decoration: none;
  position: relative;
}
#header .select-city-menu .mcs-module .city:after {
  content: '';
  border-color: #878787 transparent transparent;
  border-style: solid;
  border-width: 7px 4px;
  position: absolute;
  top: 5px;
  width: 0px;
  right: -16px;
}
#header .fixed_menu {
  float: right;
}
#header .fixed_menu .slide_menu {
  display: block;
  margin: 0 auto;
  padding: 0;
  cursor: pointer;
  outline: none;
  position: relative;
}
#header .fixed_menu .slide_menu .slide_menu-btn {
  height: 45px;
  width: 48px;
  padding: 20px 16px;
}
#header .fixed_menu .slide_menu .slide_menu-btn .slide_menu-btn-two_border {
  display: block;
  width: 100%;
  border-top: 1px solid #ffffff;
  border-bottom: 1px solid #ffffff;
  height: 6px;
}
#header .fixed_menu .slide_menu .slide_menu-btn .hidden-slide_menu-module {
  position: absolute;
  background: #272727;
  top: 55px;
  right: -65px;
  padding: 15px 35px;
  display: none;
}
#header .fixed_menu .slide_menu .slide_menu-btn .hidden-slide_menu-module:after {
  content: '';
  border-color: transparent transparent #272727;
  border-style: solid;
  border-width: 12px;
  position: absolute;
  top: -18px;
  left: 0;
  width: 0px;
  right: 0;
  margin: 0 auto;
}
#header .fixed_menu .slide_menu .slide_menu-btn .hidden-slide_menu-module div {
  padding: 3px 0;
}
#header .fixed_menu .slide_menu .slide_menu-btn .hidden-slide_menu-module div a {
  color: #D3D3D3;
}
#header .fixed_menu .slide_menu .slide_menu-btn .hidden-slide_menu-module div a:hover {
  color: #419FD9;
}
#header .top_phone {
  margin-left: 17px;
  height: 3rem;
  padding: 10px 0;
  float: right;
}
#header .top_phone a {
  font-size: 16.9px;
  line-height: 3rem;
  color: #D3D3D3;
  text-decoration: none;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}
@media only screen and (max-width: 670px) {
  #header .flex {
    display: flex;
    justify-content: space-between;
  }
  #header .flex .top_phone {
    margin: 0px;
  }
  #header .flex .top_phone .fa-phone {
    font-size: 21px;
  }
}
#menu {
  background: #E2E2E2;
}
#menu .items {
  display: flex;
  justify-content: space-between;
  float: left;
  width: 85%;
}
#menu .items .item {
  padding: 15px 13px 9px;
  margin: 0;
  font-size: 16px;
  border-bottom: 4px solid #E2E2E2;
}
#menu .items .item:hover,
#menu .items .item.active {
  color: #08c;
  border-bottom: 3px solid #08c;
}
#menu .items .item:hover a,
#menu .items .item.active a {
  text-decoration: none;
  color: #08c;
}
#menu .basket {
  float: right;
  width: 50px;
  cursor: pointer;
}
#menu .basket:hover {
  background: #000000;
}
#menu .basket:hover div {
  background: url('/repair/images/icon-basket-hover.png') 0px 0px no-repeat;
  padding: 25px 0;
  margin: 0 auto;
  width: 18px;
  height: 20px;
  background-position: center;
}
@media only screen and (max-width: 670px) {
  #menu {
    position: absolute;
    top: 48px;
    width: 100%;
    display: none;
  }
  #menu .items {
    display: block;
    width: 100%;
    float: none;
  }
  #menu .items .item {
    padding: 10px 15px;
    margin: 0 -15px;
    border-bottom: 1px solid #272727;
  }
  #menu .items .item:hover {
    border-bottom: 1px solid #272727;
    color: #08c;
  }
  #menu .basket {
    display: none;
  }
}
#footer {
