}
@media only screen and (max-width: 768px) {
  #footer .custom-menu_in_footer {
    width: 80%;
  }
  #footer .custom-menu_in_footer ul li {
    width: 25%;
  }
  #footer .footerCards {
    display: none;
  }
  #footer .custom_footerInfo {
    padding: 17px 0 0 0;
    width: 20%;
    float: left;
  }
  #footer .custom_footerInfo .footerSocial {
    width: 100%;
    padding: 0px;
  }
  #footer .custom_footerInfo .footerSocial span {
    margin-bottom: 8px;
    font-size: 12px;
    text-align: center;
  }
  #footer .custom_footerInfo .footerSocial div {
