#footer .social {
  padding: 10px 0px;
  border-bottom: 1px solid #E2E2E2;
  font-size: 1.8em;
  display: flex;
  justify-content: space-around;
}
#footer .social .fa {
  color: #444444;
}
.spinner__mod {
  background-color: rgba(255, 255, 255, 0.3);
  bottom: 0;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 9999;
  display: none;
}
.spinner__mod .loader {
  margin-left: -23px;
  margin-top: -14px;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 85px;
  height: 85px;
  display: inline-block;
  padding: 0px;
  border-radius: 100%;
  border: 3px solid;
  border-top-color: rgba(249, 132, 29, 0.6);
  border-bottom-color: rgba(249, 132, 29, 0.6);
  border-left-color: rgba(249, 132, 29, 0.15);
  border-right-color: rgba(249, 132, 29, 0.15);
  -webkit-animation: preloader3 0.8s ease-in-out infinite alternate;
  animation: preloader3 0.8s ease-in-out infinite alternate;
}
.spinner__mod span {
  color: #a2afbc;
  display: block;
  font-size: 12px;
  left: 50%;
  margin-left: -7px;
  margin-top: 51px;
  position: absolute;
  top: 43%;
}
.loading__mod {
  background-color: rgba(255, 255, 255, 0.3);
  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 9999;
  display: none;
}
.loading__mod .loading {
  margin-left: -30px;
  margin-top: -50px;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 50px;
  height: 50px;
  display: inline-block;
  padding: 0px;
  border-radius: 100%;
  border: 3px solid;
  border-top-color: rgba(218, 36, 26, 0.6);
  border-bottom-color: rgba(218, 36, 26, 0.6);
  border-left-color: rgba(218, 36, 26, 0.15);
  border-right-color: rgba(218, 36, 26, 0.15);
  -webkit-animation: preloader3 0.8s ease-in-out infinite alternate;
  animation: preloader3 0.8s ease-in-out infinite alternate;
}
@keyframes preloader3 {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@-webkit-keyframes preloader3 {
  from {
    -webkit-transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
  }
}
