.repair .call-form-content .description {
  font-size: 14px;
  color: #D3D3D3;
  margin-bottom: 30px;
}
.repair .call-form-content.salon h3 {
  padding: 5px 0;
  margin: 10px 0 30px;
}
.repair .call-form-content.salon .form__mod {
  width: 80%;
  margin: 0 auto;
}
@media only screen and (max-width: 768px) {
  .repair .call-form-content {
    height: auto;
    padding-bottom: 35px;
  }
  .repair .call-form-content.salon .form__mod {
    width: 100%;
  }
}
@media only screen and (max-width: 670px) {
  .repair .call-form-content .form__mod {
    width: 100%;
  }
  .repair .call-form-content .form__mod label {
    text-align: center;
  }
  .repair .call-form-content .form__mod .form-group {
    width: 100%;
    margin-right: 0px;
  }
  .repair .call-form-content .form__mod .form-group .btn.circle {
    width: 100%;
  }
}
.repair .return-problem {
  background: #F6F6F6;
  text-align: center;
  padding: 25px 0px;
  height: 220px;
}
.repair .return-problem .price {
  font-size: 24px;
  padding: 5px 0;
  margin-top: 20px;
  color: #08c;
  font-weight: bold;
}
.repair .description-seo.padding {
  padding: 30px 0px;
}
.repair .description-seo .text {
  font-size: 14px;
}
.repair .apple-service {
  margin-bottom: 10px;
}
.repair .apple-service .item {
  padding: 25px 0;
  border-bottom: 1px solid #E2E2E2;
  width: 70%;
  margin: 0 auto;
}
.repair .apple-service .item:last-child {
  border-bottom: none;
}
.repair .apple-service .item img {
  width: 140px;
}
.repair .apple-service .item .address-service .title {
  font-weight: bold;
  font-size: 16px;
  margin-bottom: 5px;
}
.repair .apple-service .item .address-service .address {
  padding: 2px;
}
.repair .apple-service .item .address-service .metro {
  padding: 2px 0px 0px 20px;
  background: url('/repair/images/metro.png') 0px 0px no-repeat;
  background-size: 18px;
  background-position: 0 3px;
}
.repair .apple-service .item .address-service .time {
  padding: 2px;
}
.repair .apple-service .item .address-service .phone {
  font-weight: bold;
  margin-top: 12px;
}
.repair .apple-service .item .total {
  border: 1px solid #E2E2E2;
  padding: 10px;
}
.repair .apple-service .item .total .problems {
  padding: 5px 0px;
}
.repair .apple-service .item .total ._icon-ap2 {
  padding: 7px 2px 2px 25px;
  font-size: 16px;
}
@media only screen and (max-width: 768px) {
  .repair .apple-service .item {
    width: 100%;
  }
}
@media only screen and (max-width: 670px) {
  .repair .apple-service .item .image {
    width: 33.33333333%;
  }
  .repair .apple-service .item .image img {
    width: 100px !important;
  }
  .repair .apple-service .item .address-service {
    width: 66.66666667%;
  }
  .repair .apple-service .item .block {
    width: 100%;
    padding-top: 20px;
  }
  .repair .apple-service .item .block .problems,
  .repair .apple-service .item .block .content {
    float: left;
    width: 50%;
  }
}
.repair .info-call {
  background: #272727;
  color: #D3D3D3;
  padding: 10px 0 15px;
  margin-top: 50px;
  text-align: center;
}
.repair .info-call .phone {
  font-size: 1.8em;
  padding: 5px 0;
  margin-top: 10px;
}
